import React, { Suspense, lazy } from "react";
const Author = lazy(() => import('../../Elements/Author/Author'));
const Time = lazy(() => import('../../Elements/Time/Time'));
const Network = lazy(() => import('../../Elements/Network/Network'));
const Seprator = lazy(() => import('../../Elements/Seprator/Seprator'));
const AuthorDetails = ({ Post, CardStyle }) => {
  const { author, networkId } = Post
  const postAuthor = (CardStyle.author.status && !author.isInstaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !author.isInstaUser) ? true : false;

  return (
    <div className={`tb_gt_author_wrapper `} aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_gt_author">

        {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_gt_author_profile'} /></Suspense> : ''}
        <div className="tb_gt_author_info">
          {postAuthor ? <div className="tb_gt_authorname">{author.name}</div> : ''}
          <div className="tb_gt_post_info">
            {postAuthor ? <div className="tb_gt_username">{`@${author.username}`}</div> : ''}
            {authorSeprator ? <Suspense fallback={<></>}><Seprator sepClass={`tb_gt_seprator`} /></Suspense> : ''}
            {CardStyle.timeStatus ? <Suspense fallback={<></>}><Time createdAt={Post.createdAt} timeClass={`tb_gt_time tb-cTBfont-${CardStyle.author.variant}`} /></Suspense> : ''}
          </div>
        </div>
      </div>
        <div className="tb_gt_social_">
          <Suspense fallback={<></>}><Network networkClass={'tb_gt_social__ico'} networkId={networkId} iconStyle={CardStyle.icon}  /></Suspense>
        </div>
    </div>
  );
}
export default AuthorDetails